.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.movie-card {
  width: 350px; 
  margin-bottom: 10px;
  box-sizing: border-box;
}

.movie-card img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.bg-color{
  background-color: black;
}
.head-color{
   color: red;
   font-size: 6rem;
}
.text-color{
  color:gray;
}
.active{
  background-color: red;
  color: white;
}
.btn{
    padding: 0.75rem 2rem;
    line-height: 1;
    border-radius: 1rem;
    box-shadow: 0 0 1px rgb(0 0 0/0%);
    border: 1px solid transparent;
    display: inline-block;
    font-size: 1.95rem;
}
.mr-20{
  margin:20px;
}
.padding-20{
  padding: 20px;
}
.fnt-2{
  font-size: 2.2rem;
}
.fnt-4{
  font-size: 4rem;
}
